var loadEnTranslations = function loadEnTranslations(callback, scope) {
  return import(
  /* webpackChunkName: "en-translations" */
  'en.js').then(function (module) {
    callback.call(scope, module);
  }).catch(function (error) {
    return console.log('An error occurred while loading en.js' + "\n" + error);
  });
};

